import { useState } from 'react';
import { TextField, Button } from '@mui/material';
import AddTaskOutlinedIcon from '@mui/icons-material/AddTaskOutlined';
import { toast } from 'react-toastify';
import './percentage.css';

export default function Percentage({ onSubmit }) {
  const [percente, setPercente] = useState(25);

  const handleChangePercente = e => {
    const { value } = e.currentTarget;
    setPercente(value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    onSubmit(percente);
    toast.success(`–ù–∞—Ü—ñ–Ω–∫–∞ –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∞ –Ω–∞ —Ä—ñ–≤–Ω—ñ ${percente} –≤—ñ–¥—Å–æ—Ç–∫—ñ–≤`);
  };

  return (
    <>
      <p>–ü–æ—Ç–æ—á–Ω–∞ –Ω–∞—Ü—ñ–Ω–∫–∞ —Å—Ç–∞–Ω–æ–≤–∏—Ç—å: {percente} %</p>
      <form className="percentage" onSubmit={handleSubmit}>
        <TextField
          autoComplete="off"
          name="percente"
          type="number"
          onChange={handleChangePercente}
          value={percente}
        ></TextField>

        <Button type="submit">
          <AddTaskOutlinedIcon fontSize="large" />
        </Button>
      </form>
    </>
  );
}
